function Projets(){
    return(
        <div className=" mb-3 bg-dark text-white p-4" >
            <div className="row g-0">
                <div className="col-md-8 p-4">
                    <h2 className="card-title text-uppercase">Les lements du site:</h2>
                    <br/>
                    <h4 className="text-uppercase">La V1:</h4>
                    <p className="card-text">Lors de la premire version, le site disposait d'un blog simple permettant de creer, modifier, supprimer et voir les posts. 
                    J'y ai ensuite implant un rate limiter permettant d'eviter de spammer la cration de posts, empechant ainsi la base de donnes de crasher sous l'afflux 
                    massif de requetes!
                    </p>
                    <br/>
                     <button className="btn btn-light">   
                        <Link className="nav-link" to="/blog">
                            Accder au blog
                        </Link>  
                    </button>
                </div>
                <div className="col-md-4 p-5">
                    <img src={CardBlog} className="img-fluid rounded" alt="cardblog"/>
                </div>
                <div className="col-md-8 p-4">
                    <h4 className="text-uppercase">La V2:</h4>
                    <p className="card-text">Afin d'offrir plus de scurit au blog, j'ai dcid d'implanter un systeme d'authentification ce qui a ncessit de changer la structure
                    de mon back et de mon front. J'ai en effet utilis Mongoose pour maintenant traiter les infos de ma base de donnes alors que je ne l'utilisait pas pour les
                    posts du blog auparavant. D'autre part j'ai choisi volontairement de tester l'adaptabilit de mon back avec le front en crant une structure diffrente du front
                    pour mes diffrentes requetes. Le blog utilise donc une mthode de fetch alors que le systeme d'authentification utilise react-redux-toolkit.
                    </p>
                    <br/>
                     <button className="btn btn-light">   
                        <Link className="nav-link" to="/register">
                            S'inscrire
                        </Link>  
                    </button>
                    <br/>
                   
                </div>
                <div className="col-md-4 p-5">
                    <img src={CardAuth} className="img-fluid rounded border" alt="cardblog"/>
                </div>
                <br/>
                    <p className="p-4">Le site utilise maintenant un cookie de connexion. Bien qu'il ne soit pas possible d'obtenir le mot de passe, il est toutefois possible d'obtenir 
                        des donnes concernant le pseudo, l'id et l'email de l'utilisateur, ce qui constitue une faille de scurit majeure qui devra tre fixe dans la prochaine 
                        version.
                    </p>
            </div>
        </div>
    )
}

function Next(){
    return (
    <div className="container-fluid p-5">
        <h2 className="text-uppercase">Patch notes des versions prcdentes</h2>
        <h4 className="text-uppercase">Version 1.0:</h4>
        <p>
            -Dploiement du site sur Heroku; 
            <br/>
            -Mise en place du blog link a la base de donnes mongoDB.
        </p>
        <h4 className="text-uppercase">Version 1.5:</h4>
        <p>
            -Mise en place d'un rate limitter pour gerer les flux de donnes lis a la cration de posts du blog.
        </p>
        <h4 className="text-uppercase">Version 2.0:</h4>
        <p>
            -Optimisation du back avec mise en place d'un systeme de schema pour la gestion des donnes;
            <br/>
            -Cryptage de donnes telles que les mots de passe.;
            <br/>
            -Implmentation d'un systeme d'authentification utilisant un jeton de connexion;
            <br/>
            -Blog ncessitant maintenant une authentification pour y accder.
        </p>
        <h4 className="text-uppercase">A venir: Version 2.1</h4>
        <p>
            -Correction des failles de scurits lies aux informations du cookie de connexion;
            <br/>
            -Amlioration gnrale de l'UX-UI;
            <br/>
            -Complexification des formulaires d'authentification et dploiement de mesures de scurit telles qu'un captcha ou d'une vrification d'emails.
        </p>

        <h2 className="text-uppercase">Futurs Ajouts:</h2>
        <p>
            -Ajout de nouvelles fonctionnalits au blog; 
            <br/>
            -Amlioration gnrale de la scurit du site;
            <br/>
            -Gestion des images en vue de les ajouter au profil utilisateur et au blog;
            <br/>
            -Ajout de la possibilit de commenter les posts du blog.
        </p>
    </div>
    )
}
export default function HomePage(){
    return(
        <main className="bg-dark-subtle text-dark">
            <section>
                <HeroBanner/>
                <MarqueeMern/>
            </section>
            <section>
                <History/>
            </section>
            <section>
                <Projets/>
            </section>
            <section>
                <Next/>
            </section>
        </main>
    )
}